const personCenter = resolve => require(['@/components/ThePersonCenter'], resolve)
const index = resolve => require(['@/components/ThePersonCenter/Index/index.vue'], resolve)
const groupOrder = resolve => require(['@/components/ThePersonCenter/GroupOrder'], resolve)
// const groupOrderNull = resolve => require(['@/components/ThePersonCenter/GroupOrder/null.vue'], resolve)
const myOrder = resolve => require(['@/components/ThePersonCenter/MyOrder/OrderList'], resolve)
const myOrderDetail = resolve => require(['@/components/ThePersonCenter/MyOrder/OrderDetail'], resolve)
const myInsured = resolve => require(['@/components/ThePersonCenter/myInfo/myInsured'], resolve)
const myCollect = resolve => require(['@/components/ThePersonCenter/myInfo/myCollect'], resolve)
export default [
	{
		path: '/',
		redirect: 'main'
	},
  {
    path: 'main',
    component: personCenter,
    children: [
      {
        path: '/',
        component: index,
        meta: {
          title: '个人中心',
          requireAuth: true
        }
      },
      {
        path: 'groupOrder',
        component: groupOrder,
        meta: {
          title: '个人中心-团险订单',
          requireAuth: true
        }
      },
// 			{
// 				path: 'groupOrderNull',
// 				component: groupOrderNull,
// 				meta: {
// 					title: '个人中心-团险订单',
// 					requireAuth: true
// 				}
// 			},
      {
        path: 'myOrder',
        component: myOrder,
        meta: {
          title: '个人中心-我的订单',
          requireAuth: true
        }
      },
      {
        path: 'myInsured',
        component: myInsured,
        meta: {
          title: '常用被保险人',
          requireAuth: true
        }
      },
      {
        path: 'myCollect',
        component: myCollect,
        meta: {
          title: '产品收藏',
          requireAuth: true
        }
      },
      {
        path: '*',
        component: resolve => require(['@/views/NotFound.vue'], resolve),
        meta: {
          title: '模块开发中'
        }
      }
    ]
  },
  {
    path: 'orderDetail/:id',
    component: myOrderDetail,
    meta: {
      title: '订单详情',
      requireAuth: true
    }
  },
  {
    path: 'groupDetail/:id',
    component: resolve => require(['@/components/ThePersonCenter/GroupOrder/OrderDetail.vue'], resolve),
    meta: {
      title: '个人中心-团险订单详情',
      requireAuth: true
    }
  },
  {
    path: '*',
    component: resolve => require(['@/views/NotFound.vue']),
    meta: {
      title: '模块开发中'
    }
  }
]
