import Vue from 'vue'
import Router from 'vue-router'

import loginRegister from './login-register'
import product from './product'
import personalCenter from './personalCenter'

Vue.use(Router)

export default new Router({
  scrollBehavior: (to, from, savedPosition) => savedPosition || { x: 0, y: 0 },
  // mode: 'history',
  routes: [
    {
      path: '/',
      redirect: '/37duc'
    },
    {
      path: '/37duc',
      name: '37duc',
      redirect: '/37duc/home',
      component: resolve => require(['@/views/Main.vue'], resolve),
      children: [
        {
          path: 'home',
          name: 'Home',
          component: resolve => require(['@/views/Home.vue'], resolve),
          meta: {
            title: '首页',
            requireAuth: false
          }
        },
        {
          path: 'user',
          name: 'User',
          redirect: '/37duc/user/smsLogin',
          component: resolve => require(['@/views/LoginRegister.vue'], resolve),
          meta: {
            title: '短信登录'
          },
          children: [
            ...loginRegister
          ]
        },
        {
          path: 'product',
          name: 'Product',
          // redirect: '/37duc/product/list',
          component: resolve => require(['@/views/HealthInsurance.vue'], resolve),
          meta: {
            title: '产品列表'
          },
          children: [
            ...product
          ]
        },
        {
          path: 'personCenter',
          name: 'PersonCenter',
          component: resolve => require(['@/views/personCenter.vue'], resolve),
          meta: {
            title: '个人中心'
            // requireAuth: true
          },
          children: [
            ...personalCenter
          ]
        },

        {
          path: '*',
          name: 'Not-found',
          meta: {
            keepAlive: true,
            title: '模块开发中'
          },
          component: resolve => require(['@/views/NotFound.vue'], resolve)
        }
      ]
    },
    {
      path: '*',
      redirect: '/37duc/404'
    }
  ]
})
