<template>
	<div class="run-input">
		<div class="search" :class="{error: error.err}">
			<input type="text" :value='value' @input="handleInput($event)" :placeholder="placeholder" @focus="focus" @blur="blur">
		</div>
		<span class="err" v-text="error.msg" v-if="error.err"></span>
		<div class="clean" v-if="value!==''&&isFocus" @mousedown="cleanValue">×</div>
	</div>
</template>

<script>
									// 事件处理: focus blur
	export default {
		props: {
			error: {
				type: Object,
				default: {
					err: false,		//提示错误
					msg: '111'		//错误信息
				}
			},
			placeholder: {
				type: String,		//就是placeholder
				default: ''
			},
			value: {
				default: ''
			}
		},
		methods: {
			handleInput(event){
				this.$emit('input',event.target.value)
			},
			cleanValue(){
				this.$emit('input','')
			},
			focus(){
				this.isFocus = true
				this.$emit('focus')
			},
			blur(){
				this.isFocus = false
				this.$emit('blur')
			}
		},
		data() {
			return {
				isFocus: false
			};
		}
	}
</script>

<style lang="scss" scoped="scoped">
@import '@/assets/styles/mixin.scss';
.run-input{
	display: inline-block;
	position: relative;
	width: 100%;
	height: 100%;
	min-width: 80px;
	min-height: 34px;
	box-sizing: border-box;
	.search{
		width: 100%;
		height: 100%;
		border: 1px solid #EDEDED;
		border-radius: 2px;
		box-sizing: border-box;
		input[type=text]{
			background: #FFFFFF;
			width: 100%;
			height: 100%;
			box-sizing: border-box;
			line-height: 16px;
			font-size: 14px;
			padding: 8px 32px 8px 12px;
			color: $color-text;
			cursor: all;
		}
		&.error{
			border-color: #FF4B4B;
		}
	}
	.err{
		position: absolute;
		top: 100%;
		right: 0;
		color: #FF4B4B;
		font-size: 12px;
		line-height: 22px;
	}
	.clean{
		position: absolute;
		right: 10px;
		top: 50%;
		margin-top: -8px;
		font-size: 16px;
		line-height: 16px;
		z-index: 19951105;
		color: #B6BBBB;
		cursor: pointer;
	}
}
</style>
