<template>
	<div class="run-select">
		<div class="cover" v-show="active"></div>
		<div class="search" :class="{error: error.err}">
			<input type="text" v-model="userInput" :readonly="!active" ref='input' @focus="handleFocus" @blur="handleBlur" :placeholder="placeholder">
		</div>
		<div class="deploy" :class="{rotate: active}"><img src="../../assets/images/form-select.png" @click="handleClick"></div>
		<div class="list" v-show="active">
			<div class="list-ctx" :style='maxHeight'>
				<div class="item" v-for="(option,index) in filterOptions" @mousedown="handleSelect(option)">
					<div class="in" :class="{disabled: option.disabled}">{{isSingle(option) ? option : option.label}}</div>
				</div>
				<div class="none" v-if="!filterOptions.length&&options.length">无匹配项</div>
				<div class="none" v-if="!options.length">无可选项</div>
			</div>
		</div>
		<span class="err" v-text="error.msg" v-if="error.err"></span>
	</div>
</template>

<script>
// 	事件处理: focus blur handlechange( newvalue, oldvalue )
// 	列表项: 参数名 options; 
//			值可以是 字符串数组 或 [
// 				{
// 					val: 'String',	 //必须 选项的值
// 					select: true, 	 //可选 是否默认选中
// 					disabled: false	 //可选 是否禁用
// 					...其他自定义参数
// 				}
// 			]
	export default {
		name: 'runSelect',
		props: {
			options: {
				type: Array,
				default(){		// ↑ ↑ ↑
					return []
				},
				validator(value){
					return	value!==undefined && value.constructor == Array && value.filter(val=>val.select).length <= 1
				}
			},
			limit: {
				type: Number,
				default: 0 		//下拉列表以滚动方式展示多少条数据，值小于1全部展示
			},
			error: {
				type: Object,
				default() {
					return {
						err: false, //提示错误
						msg: ''		//错误信息
					}
				}
			},
			placeholder: {
				type: String,
				default: ''		//就是placeholder
			},
			value: ''
		},
		data() {
			return {
				copy_options: [],
				userInput: '',
				copy_userInput: '',
				active: false,
				ownVal: ''
			};
		},
		methods: {
			handleFocus(){
				this.active = true
				this.$refs.input.select()
				this.$emit('focus')
			},
			handleBlur(){
				if(!this.active)return;
				// console.log('blur !')
				this.active = false;
				this.copy_userInput = '';
				let option = this.selectOption;
				if(option && !option.select){
					let selected = this.copy_options.find(opn=>opn.select&&opn!==option);
					if(selected){
						this.$set(selected,'select',false)
					}
					if( this.isSingle(option) ) {
						this.userInput = this.ownVal = option
					}else{this.$set(option,'select',true)}
				}else{
					this.userInput = this.isSingle(this.ownVal) ? this.ownVal : this.ownVal.label ;
				}
				this.$emit('blur')
// 				setTimeout(()=>{
// 					console.log('%c [run-ui]: setTimeout 100 ownVal '+this.ownVal, 'color: #000;font-weight: 600;')
// 				},600)
			},
			handleSelect(option){
				// console.log('mousedown !')
				if(!option.disabled){
					this.selectOption = option;
				}
			},
			handleClick(){
				this.active ? this.$refs.input.blur() : this.$refs.input.focus();
			},
			reset(){
				this.active = false;this.copy_options = [];this.copy_userInput = '';
				this.$refs.input && this.$refs.input.blur();
			},
			isSingle(option){
				let e = Object.prototype.toString.call(option);
				e = e.slice(1,e.length-1).split(' ')[1];
				return e==='String' || e==='Number';
			}
		},
		watch: {
			userInput(val) {
				// console.log('watch userInput !',val)
				if(this.active){
					// console.log('change copy_userInput',val)
					this.copy_userInput = val;
				}
			},
			options: {
				handler(val){
					// console.log('options')
					this.reset();
					this.copy_options = JSON.parse(JSON.stringify(this.options));
				},
				deep: true
			},
			copy_options: {
				handler(val){
					// console.log('watch copy_options !')
					const v = val.find(option=>option.select);
					if(v!==undefined){
						this.userInput = this.isSingle(v) ? v : v.label;
						this.ownVal = v
					}
				},
				deep: true
			},
			ownVal: {
				handler(nval,oval) {
					// console.log('watch ownVal   ',val)
					this.userInput = this.isSingle(nval) ? nval : nval.label;
					this.$emit('input',this.isSingle(nval) ? nval : nval.value);
					this.$emit('handlechange',nval,oval)
				}
			},
			value: {
				handler(val) {
					let select = this.copy_options.find(option=>{
						const op = this.isSingle(option) ? option : option.value;
						if(op!==undefined) {
							return op === val;
						}
						return false;
					});
					if(select!==undefined) {
						this.copy_options.forEach(option=>this.isSingle(select) || this.$set(option,'select',option===select));
					}
				}
			}
		},
		computed: {
			filterOptions() {
				return this.copy_options.filter(option=>{
					const p = this.isSingle(option) ? option+'' : option.label;
					return p.indexOf(this.copy_userInput)>-1
				})
			},
			maxHeight(){
				return this.limit<1 ? {} : {
					maxHeight: this.limit * 34 + 'px'
				}
			}
		},
		mounted(){
			this.userInput = this.ownVal;
			this.copy_options = JSON.parse(JSON.stringify(this.options));
		}
	}
</script>
x
<style lang="scss" scoped>
@import '@/assets/styles/mixin.scss';
.run-select{
	display: inline-block;
	position: relative;
	width: 100%;
	height: 100%;
	min-width: 80px;
	min-height: 34px;
	box-sizing: border-box;
	.cover{
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		z-index: 19951105;
		cursor: pointer;
	}
	.search{
		width: 100%;
		height: 100%;
		border: 1px solid #EDEDED;
		border-radius: 2px;
		box-sizing: border-box;
		input[type='text']{
			width: 100%;
			height: 100%;
			box-sizing: border-box;
			line-height: 16px;
			font-size: 14px;
			padding: 8px 32px 8px 12px;
			color: $color-text;
			background-color: #FFFFFF;
			cursor: pointer;
			&[readonly]{
				background-color: #F9F9F9;
			}
		}
		&.error{
			border-color: #FF4B4B;
		}
	}
	.deploy{	
		position: absolute;
		right: 10px;
		top: 50%;
		margin-top: -3px;
		width: 12px;
		height: 6px;
		z-index: 1;
		cursor: pointer;
		img{
			display: block;
			width: 100%;
			height: 100%;
		}
		&.rotate{
			transform: rotate(180deg);
		}
	}
	.list{
		position: absolute;
		top: 123%;
		left: 0;
		width: 100%;
		overflow: hidden;
		padding: 3px 0;
		box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
		background-color: #FFFFFF;
		border-radius: 4px;
		z-index: 19951105;
		.list-ctx{
			margin-right: -17px;
			padding: 0;
			text-indent: 1em;
			font-size: 14px;
			line-height: 34px;
			overflow-y: scroll;
			box-sizing: border-box;
			color: #606266;
			.item{
				height: 34px;
				line-height: 34px;
				overflow: hidden;
				.in{
					cursor: pointer;
					overflow-x: scroll;
					white-space: nowrap;
					&:not(.disabled):hover{
						background-color: #f5f7fa;
					}
					&.disabled{
						color: #FFFFFF;
						background-color: #F0F0F0;
						cursor: not-allowed;
					}
				}
			}
		}
	}
	.err{
		position: absolute;
		right: 0;
		top: 100%;
		color: #FF4B4B;
		line-height: 22px;
		font-size: 12px;
	}
}
</style>
