const productDetail = resolve => require(['@/components/product/detail/TheDetailMain.vue'], resolve)
const orderComfirmMain = resolve => require(['@/components/order/TheOrderComfirmMain.vue'], resolve)
const insureResult = resolve => require(['@/views/insureResult.vue'], resolve)
const sample = resolve => require(['@/components/product/detail/TheSample'], resolve)
const protocal = resolve => require(['@/components/product/detail/TheProtocal'], resolve)
export default [
  {
    path: 'list',
    name: 'ProductList',
    component: resolve => require(['@/views/ProductList'], resolve),
    meta: {
      title: '产品列表',
      requireAuth: false
    }
  },
  {
    path: 'detail/:id',
    name: 'ProductDetail',
    component: productDetail,
    meta: {
      title: '产品详情',
      requireAuth: false
    }
  },
  {
    path: 'orderConfirm',
    name: 'OrderConfirm',
    component: orderComfirmMain,
    meta: {
      title: '订单确认',
      requireAuth: true
    }
  },
  {
    path: '/insuranceResult/:orderSn',
    name: 'Success',
    component: insureResult,
    meta: {
      title: '投保结果',
      requireAuth: true
    }
  },
  {
    path: 'sample',
    name: 'Sample',
    component: sample,
    meta: {
      title: '保单样本书',
      requireAuth: false
    }
  },
  {
    path: 'protocal',
    name: 'Pample',
    component: protocal,
    meta: {
      title: '保险条款',
      requireAuth: false
    }
  },

  {
    path: '*',
    component: resolve => require(['@/views/NotFound.vue'], resolve),
    meta: {
      title: '模块开发中'
    }
  }
]
